<template>
<v-container grid-list-xl fluid>
    <v-layout row wrap>
        <v-flex sm12>
            <h3>Enviar arquivo</h3>
        </v-flex>
        <v-flex xs12 sm12 md12>
            <v-card>
                <v-flex xs12 sm12 md12>
                    <v-form ref='uploadForm' id='uploadForm' v-bind:action='mUrl' method='post' encType="multipart/form-data">
                        <v-text-field v-model="title" label="titulo" required></v-text-field>
                        <input class="v-btn" type="file" name="sampleFile" />
                        <input class="v-btn primary" type='submit' value='Upload!' />
                </v-form>
                </v-flex>
            </v-card>
        </v-flex>

    </v-layout>
</v-container>
</template>

<script>
export default {
    data() {
        return {
            valid: false,
            user: null,
            title: '',
            rota: 'http://191.252.200.167:3000/file/send',
            url: '',
        }
    },
    methods: {
    },
   mounted() {
        this.user = this.user = JSON.parse(this.$cookie.get("user"));
    },
    computed: {
        mUrl() {
            if (this.user != null) {
                return this.url = this.rota + '/' + this.title + '/' + this.user.data.id;
            }
        }
    }
}
</script>
